.cards {
  padding: 4rem;
  background-image: linear-gradient(#0B182B, #1F2C40 20%);
  font-family: 'Raleway', Verdana, sans-serif;
}

h1 {
  text-align: center;
  color: #FFFFFF;
  font-size: 48px;
}

h3 {
   text-align: center;
   color: #0B182B;
   margin-top: 24px;
   margin-bottom: 24px;
   font-size: 24px;
   line-height: 3rem;
}

.cards > h3 {
  color: #FFFFFF;
}

h3 > span {
  color: #0B1820;
  background-color: #F4C430;
  border-radius: 5px;
  padding: 5px;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 70%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__button {
  background-color: #F4C430;
  width: 50%;
  align-self: center;
  text-align: center;
  margin: 0px 0px 25px;
  padding: 10px;
  border-radius: 15px;
  color:#0B182B;
}

.cards__button a:visited {
  color:#1F2C40;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
  background-color: #9Ca1ab;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 0 1vw #0B182B;
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  top: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 16px;
  font-weight: 700;
  color: #0B1820;
  background-color: #F4C430;
  box-sizing: border-box;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;

  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 30px 30px;
}

.cards__item__text {
  color: #0B182B;
  font-size: 18px;
  font-weight: 400;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }

  .cards__container {
    width: 100%;
  }

  h3 {
    font-size: 32px;
  }

  .cards__item__text {
    font-size: 28px;
  }

  .cards__button {
    font-size: 32px;
  }
}