.footer-container {
  background-color: #0B182B;
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-header-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
}

.footer-header-section > p {
  font-family: 'Raleway,' Verdana, sans-serif;
  font-weight: 700;
}

.footer-heading {
  font-size: 48px;
}

.footer-input {
  padding: 8px 20px;
  border-radius: 2px;
  margin-right: 10px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #fff;
}

.footer-links {
  width: 60%;
  max-width: 600px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 40px auto 24px auto;
}

.footer-links i {
   margin-right: .5rem;
}

.footer-link-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 16px;
  text-align: center;
  width: 160px;
  box-sizing: border-box;
}

.footer-link-items > h2 {
  color: #fff;
}

.footer-link-items span {
   font-size: 1rem;
}

.footer-link-items a {
  color: #fff;
  text-decoration: none;
}

.footer-link-items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

.footer-email-form h2 {
  margin-bottom: 2rem;
}

.footer-links-wrapper {
   display: flex;
}

.footer-input::placeholder {
  color: #b1b1b1;
}

/* Social Icons */
.social-icon-link {
  color: #fff;
  font-size: 24px;
}

.social-media {
  max-width: 1000px;
  width: 100%;
  border-top: 1px solid #F4C430;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}

.social-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.website-rights {
  color: #fff;
  margin-bottom: 16px;
}

.credits {
  display: flex;
  max-width: 50%;
}

@media screen and (max-width: 820px) {
  .footer-links {
    flex-direction: column;
  }

  .footer-link-items {
   text-align: center;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}